<template>
  <!-- 确认提示框组件 -->
    <div class="confirm">
      <div class="confirm_context" 
      :style="{
        top:top +'px', 
        opacity : opacity,
        backgroundColor : bgColor,
        width : width +'px',
        padding :padding +'px',
        borderRadius : borderRadius + 'px'
      }">
        <!-- 标题 -->
        <p class="confirm_tit"
        :style="{
          textAlign:tittextAlign,
          color:titColor,
          lineHeight: titlineHeight + 'px',
          fontSize: titfontSize + 'px',
          fontWeight: titfontWeight,
        }">{{ title }}</p>
        <!-- 内容 -->
        <p class="confirm_msg"
        :style="{
          textAlign:contextAlign,
          color:conColor,
          lineHeight: conlineHeight + 'px',
          fontSize: confontSize + 'px',
          fontWeight: confontWeight,
        }">{{ content }}</p>
        <div class="confirm_btn">
          <span @click="close();off()" v-if="buttonConfigs.no">{{ buttonConfigs.no }}</span>
          <span @click="ok();off()" v-if="buttonConfigs.yes">{{ buttonConfigs.yes }}</span>
        </div>
      </div>
    </div>
</template>
<script>
import { onMounted, ref } from "vue";
export default {
  name: "confirm",
  props: {
    // 标题
    title:{
      type:String,
      default:"标题"
    },
    // 提示信息
    content:{
      type:String,
      default:"默认提示信息"
    },
    // 弹框样式
    top:{
      type:Number,
      default:50
    },
    bgColor:{
      type:String,
      default:"#fff"
    },
    width:{
      type:Number,
      default:300
    },
    padding:{
      type:Number,
      default:10
    },
    borderRadius:{
      type:Number,
      default:2
    },
    // 标题样式
    tittextAlign:{
      type:String,
      default:"center"
    },
    titColor:{
      type:String,
      default:"#000"
    },
    titlineHeight:{
      type:Number,
      default:20
    },
    titfontSize:{
      type:Number,
      default:16
    },
    titfontWeight:{
      type:Number,
      default:700
    },
    // 内容样式
    contextAlign:{
      type:String,
      default:"center"
    },
    conColor:{
      type:String,
      default:"#777"
    },
    conlineHeight:{
      type:Number,
      default:22
    },
    confontSize:{
      type:Number,
      default:14
    },
    confontWeight:{
      type:Number,
      default:100
    },

    // 按键配置
    buttonConfigs:{
      type: Object,
      default: () => {
        return  {
            yes: "确定",
            no: "取消",
        }
      },
    },
    // 确认事件在config.js中定义
    ok: {
      type: Function,
      required: true
    },
    // 取消事件在config.js中定义
    close: {
      type: Function,
      required: true
    },
  },
  setup(props,ctx) {
    // 初始化高度,在传入参数的高度上-50，因为动画移动时会+50
    const top = ref(props.top - 50) 
    // 初始化隐藏
    const opacity = ref(0)
    // 显示时
    setTimeout(() => {
        top.value += 50
        opacity.value = 1
    }, 0);
    // 确认和取消都隐藏弹窗
    const off = function (params) {
        top.value -= 50
        opacity.value = 0
    }
   
    return{
      top,
      opacity,
      off
    }
  }
};
</script>
<style>

/* 外层遮罩层 */
.confirm {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
.confirm_context {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  border-radius: 5px;
  box-sizing: border-box;
  transition: .2s;
}

.confirm_msg {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.confirm_btn {
  overflow: hidden;
}
.confirm_btn span {
  font-size: 14px;
  cursor: pointer;
  float: right;
  height: 30px;
  width: 50px;
  text-align: center;
  line-height: 30px;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 5px;
  color: rgb(122, 122, 122);
  transition: 0.3s;
}
.confirm_btn span:hover {
  color: #fff;
  background: rgb(0, 60, 255);
}
</style>
