import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/notes',
    children:[
      {
        path:'/notes',
        name:'notes',
        component:()=>import ('../pages/notes.vue'),   //文章列表
      },
      {
        path:'/assembly',
        name:'assembly',
        component:()=>import ('../pages/assembly/assembly.vue'),   //组件页面
      },
      {
        path:'/details',
        name:'details',
        component:()=>import ('../pages/details.vue'),    //文章详情
      },
      {
        path:'/about',
        name:'about',
        component:()=>import ('../pages/about.vue'),     //个人介绍
      },
    ]
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
