import { createApp  } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// markdown样式
import "github-markdown-css";
// 代码高亮
import "./assets/css/swh.css"; //默认样式

// 字体图标
import './assets/iconfont/iconfont.css';
// 电子数字字体
import './assets/css/fonts/font.css';   

// // 引入md文章组件列表
import  { mdArticle } from './article/index.js'
// 全局组件
import { SupTopUI } from './components/component_module/index.js'

const app = createApp(App)
app.use(SupTopUI)
app.use(mdArticle)
app.use(store)
app.use(router)
app.mount('#app')


